import http from "../http-common";
import authHeader from '@/services/auth-header'
class TutorialDataService {
  csv(data) {
    return http.post("/csv/upload",data);
  }
  getAll() {
    return http.get("/usersAll",{ headers: authHeader() });
  }
  get(id) {
    return http.get(`/usersAll/${id}`,{ headers: authHeader() });
  }

  create(data) {
    return http.post("/usersAll", data);
  }

  update(id, data) {
    return http.put(`/usersAll/${id}`, data,{ headers: authHeader() });
  }

  delete(id) {
    return http.delete(`/usersAll/${id}`,{ headers: authHeader() });
  }

  deleteAll() {
    return http.delete(`/usersAll`);
  }

  // findByTitle(title) {
  //   return http.get(`/csv?title=${title}`);
  // }
}

export default new TutorialDataService();